import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import {mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {
			flag: true,//用于判断用户购物车是否有商品，没有商品为true，有商品为false
			myData: [],
			allchecked: false ,
			sessionId:'',
			cartList:[],
			disabled:true,
			submitPrice:'',
			payPwd:'',
			keyNumberList:[1,2,3,4,5,6,7,8,9,0,'.','支付'],
			number:[],
			chat:[]
		}
	},
	onLoad() {
		this.init()
	},
	computed: {
		...mapState(['hasLogin','uerInfo']), 
		
		// 计算选中商品数量
		totalNum() {
			let totalNum = 0;
			this.myData.map(item => {
				item.flag ? totalNum += 1 : totalNum += 0
			})
			return totalNum
		},
		
		//计算选中商品的总价
		totalPrice() {
			let totalPrice = 0;
			this.myData.map(item => {
				item.flag ? totalPrice += item.qty * item.myPrice : totalPrice += 0
			})
			return totalPrice
		}
	},
	watch:{
		// 监听按钮禁用状态
		totalNum(val){
			if(val != 0){
				this.disabled = false
			}else{
				this.disabled = true
			}
		},
		
		// 监听总价
		totalPrice(val){
			this.submitPrice = val
		},
		
		flag(val){
			console.log(val)
		}
		
	},
	methods: {
		...mapMutations(['login']),
		// 单个商品前的勾选
		selected(item) {
			item.flag = !item.flag
			if (!item.flag) {
				this.allchecked = false
			} else {
				const test = this.myData.every(item => {
					return item.flag === true
				})
				if (test) {
					this.allchecked = true
				} else {
					this.allchecked = false
				}
			}
			
		},
		// 全选按钮
		selectedall() {
			this.allchecked = !this.allchecked
			if (this.allchecked) {
				this.myData.map(item => {
					item.flag = true
				})
			} else {
				this.myData.map(item => {
					item.flag = false
				})
			}
		},
		
		// 结算
		submit(){
			this.$refs.popup.open()
			this.number = []
			this.chat = []
		},
		
		// 调用接口
		clickNum(index){
			if(index < 11){
				if(index < 10){
					index += 1
					if(index === 10){
						index -= 10
					}
					if(this.number.length < 6){
						this.number += index;
						this.chat += '.'
						return
					}
				}
			}else if(index == 11){
				if(this.number.length == 0){
					uni.showToast({
						title:'请输入支付密码',
						icon:'none',
						duration:3000
					})
				}else{
					this.myData.forEach(e=>{
						if(e.flag){
							var data={
								id:e.id,
								goodId:e.goods.id,
								userId:e.userId,
								qty:e.qty,
								price:e.price,
								name:e.name
							}
							this.cartList.push(data)
						}
					})
					uni.request({
						url:'https://www.nssnail.com/wx/cartSettlement',
						method:'POST',
						data:{
							cartList:this.cartList,
							sessionId:this.sessionId,
							totalPrice:this.submitPrice,
							payPwd:this.number
						},
						success: (res) => {
							if(res.data.stat == 1){
								uni.showToast({
									title:res.data.message,
									icon:'success',
									duration:3000
								})
								this.$refs.popup.close()
								this.init()
							}else if(res.data.stat == 0){
								uni.showToast({
									title:res.data.message,
									icon:'none',
									duration:3000
								})
							}else if(res.data.stat == 3 || res.data.stat == 4){
								uni.showModal({
									title: '提示',
									content: '登录过期，请重新登录',
									success: function (res) {
										if (res.confirm) {
											uni.navigateTo({
												url:'../myInit/myInit'
											})
										}
									}
								});
							}
						}
					})
				}
			}
		},
		
		// 页面初始化
		init(){
			uni.getStorage({
				key: 'userInfo',
				success:(res) => {
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/getCart',
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							if(res.data.stat == 1){
								res.data.data.map(item =>{
									item.flag = false
								})
								this.myData = res.data.data
								// this.cartList = res.data.data
								if(this.myData.length == 0){
									this.flag = false
								}
								// 遍历myData，给条数据新增一个字段myPrice，代表每个商品的价格
								for(var i in res.data.data){
									var myPrice = this.myData[i].price/this.myData[i].qty
									this.myData[i].myPrice = myPrice
								}
							}else if(res.data.stat == 3 || res.data.stat == 4){
								uni.showModal({
								    title: '提示',
								    content: '登录过期，请重新登录',
								    success: function (res) {
								        if (res.confirm) {
								            uni.navigateTo({
								            	url:'../myInit/myInit'
								            })
								        }
								    }
								});
							}
						}
					})
				} 
			})

		},
		
		// 减号操作
		reduce(item) {
			var id = item.id
			var goodsPrice = item.myPrice
			if (item.qty > 1) {
				var myqty = --item.qty
				this.updateQty(myqty,goodsPrice,id)
			} else {
				item.qty = 1
				var myqty = 1
				this.updateQty(myqty,goodsPrice,id)
				return
			}
		},
		
		// 加号操作
		add(item) {
			var id = item.id
			var goodsPrice = item.myPrice
			var myqty = ++item.qty
			this.updateQty(myqty,goodsPrice,id)
		},
		
		// 修改商品数量
		updateQty(myqty,goodsPrice,id){
			var price = myqty * goodsPrice
			uni.request({
				url:'https://www.nssnail.com/wx/updateCart',
				method:'POST',
				data:{
					id:id,
					qty:myqty,
					price:price,
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						return this.myData
					}else if(res.data.stat == 3 || res.data.stat == 4){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		// 删除单挑购物车商品
		del(item, index) {
			var id = item.id
			console.log(id)
			uni.showModal({
			    title: '提示',
			    content: '是否删除购物车？',
			    success: (res) => {
			        if (res.confirm) {
			            uni.request({
			            	url:'https://www.nssnail.com/wx/deleteCart',
							method:'POST',
			            	data:{
			            		id:id,
			            		sessionId:this.sessionId
			            	},
			            	success: (res) => {
			            		if(res.data.stat == 1){
			            			uni.showToast({
			            				title:'删除成功',
			            				icon:'success',
			            				duration:2000,
			            				success: () => {
			            					this.init()
			            				}
			            			})
			            		}else if(res.data.stat == 3 || res.data.stat == 4){
									uni.showModal({
									    title: '提示',
									    content: '登录过期，请重新登录',
									    success: function (res) {
									        if (res.confirm) {
									            uni.navigateTo({
									            	url:'../myInit/myInit'
									            })
									        }
									    }
									});
								}else if(res.data.stat == 0){
									uni.showToast({
										title:res.data.message,
										icon:'none',
										duration:3000
									})
								}
			            	}
			            })
			        }
			    }
			});
		},
		
		// 跳转至物品页面
		gotoChoise(){
			uni.navigateTo({
				url:'../allGoods/allGoods'
			})
		}

	},
	components:{
		uniGoodsNav
	}
}
